var Utils = {
  // 
  isMobileNumber(phone) {
    if (!/^1[345678]\d{9}$/.test(phone)) {
      return false;
    } else {
      return true
    }
  },
  //   
  isTelNumber(telnum) {
    if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(telnum)) {
      return false;
    } else {
      return true
    }
  },
  //   Arr
  isObjVacancy(obj) {
    var obj = Object.keys(obj)
    var s = true
    obj.forEach(res => {
      if (obj[res] === '' || obj[res] === undefined || obj[res] === null) {
        s = false
      }
    })
    return s
  },
  //   
  isRealName(str) {
    var re1 = new RegExp("^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z0-9])*$");
    if (!re1.test(str)) {
      return false;
    }
    return true;
  },
  // url
  getUrlParam(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    //
    var str = window.location.href.split("?")[1];
    if (str) {
      var r = str.substr(0).match(reg);
      //
      if (r != null)
        return decodeURI(r[2]);
      return '';
    } else {
      return ''
    }
  },
  // 
  hasSpecSymbol(str) {
    var pattern = new RegExp("[`~!@#$^&*()=|{}':;',\\[\\].<>/?~@#&*|{}' ]");
    if (pattern.test(str)) {
      return true;
    }
    return false;
  },
  // 
  isFigure(str) {
    var pattern = new RegExp("^[0-9]+.{0,1}[0-9]{0,2}$");
    if (pattern.test(str)) {
      return true;
    }
    return false;
  },
  //   email 
  isEmail(email) {
    if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(email)) {
      return false;
    } else {
      return true
    }
  },
  //   
  isIdentity(identity) {
    // 1518151817X 
    var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
    if (reg.test(identity) === false) {
      return false;
    } else {
      return false
    }
  },
}
export default Utils
